/* 6. About */
.about__section{
    padding: 90px 0 120px 0;
    .left__shape{
        position: absolute;
        bottom: -225px;
        left: 10px;
        img{
            @include imgw;
        }
        @include breakpoint(max-xl){
            display: none;
        }
    }
    @include breakpoint(xl){
        padding: 90px 0 120px 0;
    }
    .box__shape{
        position: absolute;
        top: 125px;
        right: 100px;
        @include breakpoint(max-xl){
            display: none;
        }
    }
    .right__vector{
        position: absolute;
        bottom: 145px;
        right: 150px;
        @include breakpoint(max-md){
            display: none;
        }
    }
    .about__thumb{
        position: relative;
        &.about__thumb-2{
            clip-path: polygon(100% 0, 100% 82%, 80% 100%, 0 100%, 0 0);
        }
        .vector__shape{
            position: absolute;
            bottom: -68px;
            left: 40px;
            @include breakpoint(max-md){
                display: none;
            }
        }
        img{
            @include imgw;
        }
        .counter__wrap{
            position: absolute;
            bottom: -50px;
            left: 75px;
            right: 0;
            @include breakpoint(max-md){
               left: 30px;
            }
            @include breakpoint(md){
                bottom: 0;
            }
            .counter__wrap__item{
                gap: 2px;
                @include breakpoint(max-sm){
                    flex-wrap: wrap;
                }
                .counter__items{
                    width: 105px;
                    background-color: var(--theme);
                    padding: 17px 27px;
                    text-align: center;
                    @include breakpoint(md){
                        width: 122px;
                    }
                    @include breakpoint(xxl){
                        width: 137px;
                    }
                    h2{
                        font-size: 36px;
                        color: var(--white);
                        text-align: center;
                        @include breakpoint(md){
                            font-size: 48px;
                        }
                        @include breakpoint(xxl){
                            font-size: 58px;
                        }
                    }
                }
                .counter__content{
                    background-color: var(--theme2);
                    padding: 12px;
                    clip-path: polygon(0 1%, 88% 0, 100% 20%, 100% 80%, 100% 100%, 0 100%, 0% 80%, 0% 20%);
                    @include breakpoint(md){
                        padding: 19px;
                    }
                    @include breakpoint(xxl){
                        padding: 22px;
                    }
                    h3{
                        font-size: 22px;
                        color: var(--white);
                        @include breakpoint(xxl){
                          font-size: 24px;
                        }
                    }
                }
            }
        }
    }
    .about__content{
        p{
            color: var(--pra);
        }
        &.about__content-2{
            @include breakpoint(max-lg){
                margin-top: 80px;
            }
        }
        .title__content{
            gap: 35px;
            @include breakpoint(max-md){
                flex-wrap: wrap;
            }
            &::before{
                position: absolute;
                bottom: -24px;
                left: 0;
                border: 1px solid var(--theme2);
                content: "";
                width: 110px;
            }
        }
        .list__item{
            .list__wrap{
                gap: 20px;
                &:not(:last-child){
                    margin-bottom: 10px;
                }
                .img{
                    background: url(../img/about/box.png) no-repeat center center;
                    background-size: cover;
                    width: 36px;
                    height: 37px;
                    text-align: center;
                    line-height: 34px;
                }
            }
        }
        .about-button{
            .cmn--btn{
                padding: 12px 48px;
            }
        }
    }
    .progress__wrap{
        margin-bottom: 32px;
        @include breakpoint(max-xs){
            margin-bottom: 20px;
        }
        .pro__items{
            padding: 15px 24px 15px 0;
            width: calc(100% / 1);
            .pro__head{
                margin-bottom: 20px;
                .title{
                    font-size: 16px;
                    color: var(--theme);
                    font-weight: 700;
                }
            }
            @include breakpoint(max-xs){
                width: 100%;
                padding: 11px 24px 11px 0;
                .pro__head{
                    margin-bottom: 12px;
                }
            }
            .progress {
                border-radius: 0;
                height: 18px;
            }
        }
        .progress-bar {
            background: var(--theme2);
            color: #fefefe;
            position: relative;
            width: 100%;
            font-size: 13px;
            font-weight: 400;
            @include bodyfont;
            text-align: right;
            padding-right: 10px;
        }
        
        .progress-value {
            animation: load 3s normal forwards;
            border-radius: 0;
            background: var(--theme2);
            height: 15px;
            width: 0;
        }
        
        @keyframes load {
            0% { width: 0; }
            100% { width: 68%; }
        }
    }
}
.about__two{
    padding: 60px 0;
    @include breakpoint(md){
        padding: 100px 0;
    }
    @include breakpoint(xl){
        padding: 180px 0;
    }
    .left__shape{
        left: -65px;
        bottom: -90px;
    }
    .logo__box{
        position: absolute;
        top: 60px;
        right: 38px;
        @include breakpoint(max-xxsmall){
            display: none;
        }
        @include breakpoint(xxs){
           top: 60px;
           right: 40px;
        }
        @include breakpoint(xs){
            top: 73px;
            right: 50px;
        }
        @include breakpoint(sm){
            top: 85px;
            right: 60px;
        }
        @include breakpoint(md){
            top: 140px;
            right: 107px;
        }
        @include breakpoint(lg){
            top: 70px;
            right: 40px;
        }
        @include breakpoint(xl){
            top: 90px;
            right: 65px;
        }
        @include breakpoint(xxl){
            top: 115px;
            right: 85px;
        }
    }
    .box__shape__two{
        position: absolute;
        top: 20px;
        right: 0;
        animation-duration: 2s;
        animation: scale 2s alternate infinite;
        @include breakpoint(max-xxsmall){
            display: none;
        }
        @include breakpoint(sm){
            top: 30px;
            right: 25px;
        }
        @include breakpoint(md){
            top: 85px;
            right: 20px;
        }
        @include breakpoint(lg){
            top: 30px;
        }
        @include breakpoint(xxl){
            top: 85px;
        }
    }
    .about__thumb{
        .right__vector__two{
            position: absolute;
            left: 90px;
            bottom: 0;
            @include breakpoint(xsmall){
               left: 120px;
               bottom: 10px;
            }
            @include breakpoint(xs){
                left: 140px;
            }
            @include breakpoint(xs){
                left: 150px;
                bottom: 20px;
            }
        }
    }
    .profile__item{
        gap: 8px;
        .profile{
            gap: 17px;
        }
    }
}
//--About Section End--//
